import React from "react";
import CloseIconUrl from "./svg/ic-x.svg";
import "./Close.less";

interface Props {
    onClick?: () => void;
}

interface States {

}

export default class Close extends React.Component<Props, States> {
    handleOnClick = () => {
        this.props.onClick && this.props.onClick();
    };

    render() {
        return (
            <button className={"icon-close"} onClick={this.handleOnClick}>
                <img src={CloseIconUrl}  alt={"close"}/>
            </button>
        )
    }
}