import React from "react";
import { PostAuthor } from "@Types/model/Posts";

interface Props {
    author: PostAuthor;
    vendorType: string;
}

interface State {

}

export default class Profile extends React.Component<Props, State> {
    render() {
        const { author, vendorType } = this.props;
        return (
            <footer className={"profile"}>
                <div className={"profile-image"}>
                    <div>
                        <img src={author.profileImageUrl} />
                    </div>
                    <div className={"vendor-image"}>
                        {
                            vendorType === "TIKTOK" &&
                                <img src="/images/logo_tiktok@3x.png" />
                        }
                        {
                            vendorType === "INSTAGRAM" &&
                            <img src="/images/logo_insta@3x.png" />
                        }
                    </div>
                </div>
                <div className={"profile-desc"}>
                    <div className={"profile-name"}>{author.name}</div>
                    <div className={"post-date"}>2020.02.02</div>
                </div>
            </footer>
        )
    }
}
