/**
 * Universal(Isomorphic) Ajax Util
 * @see https://github.com/mzabriskie/axios
 */
import axios, { AxiosError, AxiosRequestConfig, AxiosResponse } from "axios";
import env from "../env";
import { REQUEST_TIMEOUT } from "../constants/api";

const axiosInstance = axios.create({
  baseURL: `${env.BASE_API_URL}`,
  timeout: REQUEST_TIMEOUT,
  withCredentials: true
});

export class ApiInterceptor {
  public static addRequest(
    onFulfilled: (config: AxiosRequestConfig) => AxiosRequestConfig | Promise<AxiosRequestConfig>,
    onRejected?: (error: AxiosError) => Promise<AxiosError> | void
  ): number {
    return axiosInstance.interceptors.request.use(onFulfilled, onRejected);
  }

  public static addResponse<T>(
    onFulfilled: (response: AxiosResponse<T>) => AxiosResponse<T> | Promise<AxiosResponse<T>>,
    onRejected?: (error: AxiosError) => Promise<AxiosError> | void
  ): number {
    return axiosInstance.interceptors.response.use(onFulfilled, onRejected);
  }
}

export default axiosInstance;
