import {PostReducer, PostState} from "@Types/reducers/Post";
import {ActionType} from "@Types/redux/actions";

const initialState: PostState = {
    posts: [],
    currentPosition: 0,
    isPostOpen: false
};

const postReducer: PostReducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionType.GET_POST:
            return {
                ...state
            };
        case ActionType.SET_POST:
            return {
                ...state,
                posts: action.posts
            };
        case ActionType.POST_POSITION_CHANGE:
            return {
                ...state,
                currentPosition: action.position
            };
        case ActionType.CHANGE_DISCOVER_POST_OPEN_STATE:
            return {
                ...state,
                isPostOpen: action.isPostOpen,
                currentPosition: action.position != null ? action.position : state.currentPosition
            };
        default:
            return state;
    }
};

export default postReducer;