import "./Discover.less";
import React from "react";
import { connect } from "react-redux";
import { RootState } from "@Types/RootState";
import { getPostList, changeDiscoverPostOpenState, changePostPosition } from "@Redux/actions/postAction";
import Post from "@Components/post/Post";
import Posts from "@Types/model/Posts";
import Masonry from "react-masonry-css";
import Close from "@Components/close/Close";
import Arrow from "@Components/arrow/Arrow";

interface StateProps {
    posts: Posts[];
    isPostOpen: boolean;
    currentPosition: number;
}

interface DispatchProps {
}

interface OwnProps {
}

interface Props extends StateProps, DispatchProps, OwnProps {
    children: JSX.Element | JSX.Element[];
    getPostList: Function;
    changeDiscoverPostOpenState: Function;
    changePostPosition: Function;
}

interface State {
}

const breakpointColumn = {
    default: 6,
    1200: 5,
    1000: 4,
    794: 3
};

const breakpointColumnOpen = {
    default: 3,
    1000: 2,
    794: 1
};

class Discover extends React.Component<Props, State> {
    postClick(position: number): void {
        this.props.changeDiscoverPostOpenState(true, position);
    }

    postClose(): void {
        this.props.changeDiscoverPostOpenState(false);
    }

    videoEnd(): void {
        const { currentPosition, posts } = this.props;
        if (currentPosition !==  posts.length - 1) {
            this.props.changePostPosition(currentPosition + 1);
        } else {
            // TODO Paging 처리
            this.props.changePostPosition(0);
        }
    }

    componentDidMount(): void {
        const { posts, getPostList } = this.props;
        if (posts.length === 0) {
            getPostList();
        }
    }

    render() {
        const { posts, isPostOpen, currentPosition } = this.props;
        const { changePostPosition } = this.props;

        return (
            <div className={"discover-post"}>
                <Masonry
                    breakpointCols={isPostOpen ? breakpointColumnOpen : breakpointColumn}
                    className={"discover-post-grid"}
                    columnClassName={"grid-column"}>
                    {
                        posts && posts.length > 0 &&
                        posts.map((it, i) =>
                            <Post
                                key={i}
                                postType={"overlay"}
                                title={it.title}
                                content={it.content}
                                author={it.author}
                                vendorType={it.vendorType}
                                onClick={() => this.postClick(i)}
                            />)
                    }
                </Masonry>
                {
                    isPostOpen && <div className={"post-detail"}>
                        <div className={"post-detail-header"}>
                            <Close onClick={() => this.postClose()} />
                            <div className={"post-detail-arrow"}>
                                <Arrow type={"prev"} hide={currentPosition === 0} onClick={() => changePostPosition(currentPosition - 1)} />
                                <Arrow type={"next"} hide={currentPosition === posts.length - 1} onClick={() => changePostPosition(currentPosition + 1)} />
                            </div>
                        </div>
                        <Post
                            title={posts[currentPosition].title}
                            content={posts[currentPosition].content}
                            author={posts[currentPosition].author}
                            vendorType={posts[currentPosition].vendorType}
                            videoPlay={true}
                            onVideoEnd={() => this.videoEnd()}
                        />
                    </div>
                }
            </div>
        );
    }
}


const mapStateToProps = (state: RootState): StateProps => {
    return {
        posts: state.postState.posts,
        currentPosition: state.postState.currentPosition,
        isPostOpen: state.postState.isPostOpen
    }
};

const mapDispatchToProps = {
    getPostList: getPostList,
    changeDiscoverPostOpenState: changeDiscoverPostOpenState,
    changePostPosition: changePostPosition
};

export default connect<StateProps, DispatchProps, OwnProps, RootState>(mapStateToProps, mapDispatchToProps)(Discover);
