import { composeWithDevTools } from "redux-devtools-extension/developmentOnly"
import { applyMiddleware, createStore } from "redux"
import { persistStore } from "redux-persist"
import middlewares, { sagaMiddleware } from "../../reduxs/middlewares/index"

import reducers from "../../reduxs/reducers"
import sagas from "../../reduxs/sagas"
import env from "../env/index";

export function configureStore() {
  const composeEnhancers = composeWithDevTools({ name: "react-boilerplate" });

  // Apply these middlewares only for dev mode
  if (env.NODE_ENV.isDevelopment()) {
  }

  const store = createStore(reducers, composeEnhancers(applyMiddleware(...middlewares)));

  sagaMiddleware.run(sagas);
  const persistor = persistStore(store, undefined);

  return {
    store,
    persistor
  }
}
