import { request } from "./request/request";
import { WebResponse } from "@Types/WebResponse";
import Posts from "@Types/model/Posts";

interface SearchPostParams {
    key: string; // koko
    hashTag: string | null;
}

interface SearchPostApi {
    (params: SearchPostParams): Promise<WebResponse<Posts[]>>;
}

export const searchPostApi: SearchPostApi = async ({ key, hashTag }): Promise<WebResponse<Posts[]>> => {
    const response: WebResponse = await request({
        url: "/post",
        method: "GET",
        data: { key, hashTag }
    });
    return response;
};
