import React from "react";
import LogoIconUrl from "./svg/logo.svg";

interface Props {
  width?: number | string;
  height?: number | string;
}

const Logo: React.FunctionComponent<Props> =
    ({width = "auto", height = "auto" }) => (
        <React.Fragment>
            <img src={LogoIconUrl} width={width} height={height} />
        </React.Fragment>
);

export default Logo;