import React from "react";
import videoJs from "video.js";
import "./Video.less";

interface State {
}

interface Props {
    videoUrl: string;
    posterUrl: string;
    className?: string;
    onVideoEnd?: () => void;
}

export default class Video extends React.Component<Props, State> {
    public player?:videoJs.Player;
    private videoNode?: HTMLVideoElement;

    componentDidMount(): void {
        const { videoUrl, onVideoEnd, posterUrl } = this.props;
        const options = {
            autoplay: true,
            controls: true,
            inactivityTimeout: 500,
            poster: posterUrl,
            sources: [{
                src: videoUrl,
                type: "video/mp4"
            }],
            controlBar: {
                fullscreenToggle: false
            }
        };

        this.player = videoJs(this.videoNode, options, () => {
            if (this.player) {
                this.player.userActive(false);
                this.player.on("ended", () => {
                    onVideoEnd && onVideoEnd();
                });
            }
        });
    }

    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<State>, snapshot?: any): void {
        if (this.player) {
            this.player.poster(this.props.posterUrl);
            this.player.src({
                src: this.props.videoUrl,
                type: "video/mp4"
            });
        }
    }

    componentWillUnmount(): void {
        if (this.player) {
            this.player.dispose();
        }
    }

    render() {
        return (
            <div className={`${this.props.className} video-koko`}>
                <div data-vjs-player>
                    <video ref={(node: HTMLVideoElement) => this.videoNode = node} className="video-js" />
                </div>
            </div>
        );
    }
}
