import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import BaseLayout from "./containers/layout/BaseLayout";
import Home from "./containers/home/Home";
import Discover from "./containers/discover/Discover";
import { ERouteUrl } from "./config/constants/routeUrls";
// import Post from "./containers/post/Post";

class Routes extends Component<any, any> {
    render() {
        return (
            <Router>
                <Switch>
                    <BaseLayout>
                        <Route exact path={ERouteUrl.HOME} component={Home}/>
                        <Route exact path={ERouteUrl.DISCOVER} component={Discover}/>
                    </BaseLayout>
                </Switch>
            </Router>
        );
    }
}

export default Routes;
