/**
 * PROFILE: local|develop|perf|production
 * 실제 서비스하는 환경
 */
export enum PROFILE {
  LOCAL = "local",
  DEVELOP = "develop",
  PERF = "perf",
  PRODUCTION = "production",
}

/**
 * NODE_ENV: development|production
 * 빌드환경 (dev-server or build 구분)
 */
export enum NODE_ENV {
  DEVELOPMENT = "development",
  PRODUCTION = "production",
}

const nodeEnvValue = (process.env.NODE_ENV as NODE_ENV);
const profileValue = (process.env.PROFILE as PROFILE);
const assetUrlValue = (process.env.ASSET_URL as string);
const publicUrlValue = (process.env.PUBLIC_URL as string);

console.log("NODE_ENV :", nodeEnvValue);
console.log("PROFILE :", profileValue);

const staticFile = require(`./${profileValue}`);
export default {
  ...staticFile,

  /**
   * NODE_ENV: development|production
   * 빌드환경 (dev-server or build 구분)
   */
  NODE_ENV: {
    getValue(): NODE_ENV { return nodeEnvValue },
    isDevelopment(): boolean { return nodeEnvValue === NODE_ENV.DEVELOPMENT },
    isProduction():  boolean { return nodeEnvValue === NODE_ENV.PRODUCTION },
  },

  /**
   * PROFILE: local|develop|perf|production
   * 실제 서비스하는 환경
   */
  PROFILE: {
    getValue(): PROFILE { return profileValue },
    isDevelopment(): boolean { return profileValue === PROFILE.DEVELOP },
    isProduction():  boolean { return profileValue === PROFILE.PRODUCTION },
  },

  /**
   * 배포 시 빌드 번호
   */
  ASSET_URL: assetUrlValue,

  /**
   * 배포 시 빌드 후 붙는 리소스 prefix 경로 (예: /resources)
   */
  PUBLIC_URL: publicUrlValue,
};
