import React from "react";
import { PostAuthor, PostContent } from "@Types/model/Posts";
import "./Post.less";
import Profile from "./Profile";
import styled from "styled-components";
import Video from "@Components/video/Video";

interface State {
    onLoad: boolean;
}

interface Props {
    onClick?: () => void;
    postType?: string;
    vendorType: string;
    title: string;
    content: PostContent;
    author: PostAuthor;
    videoPlay?: boolean;
    onVideoEnd?: () => void;
}

interface PostImageProps {
    backgroundImage: string;
}

const PostImage = styled.div<PostImageProps>`
            &:before {
                background-image : ${props => `url(${props.backgroundImage})`};
            }
        `;

export default class Post extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            onLoad: false
        };
    }

    handleOnClick = () => {
        this.props.onClick && this.props.onClick();
    };

    onLoadImage = () => {
        this.setState({
            onLoad: true
        });
    };

    render() {
        const { postType, title, vendorType, content, author, videoPlay, onVideoEnd } = this.props;
        const { onLoad } = this.state;

        return (
            <div className={`post ${postType ? postType : ""}`} onClick={this.handleOnClick}>
                {
                    videoPlay ?
                        <Video className={"post-video"}
                               videoUrl={content.videoUrl}
                               posterUrl={content.thumbnailImageUrl}
                               onVideoEnd={onVideoEnd} /> :
                        <PostImage className={"post-image"} backgroundImage={content.thumbnailImageUrl}>
                            <div className={"post-image-background"}>
                                <img onLoad={this.onLoadImage}
                                     src={content.thumbnailImageUrl}
                                     alt={title} />
                            </div>
                        </PostImage>
                }
                <Profile author={author} vendorType={vendorType} />
            </div>
        );
    }
}
