import axios from "../../../config/initializers/axios";
import { WebResponse } from "@Types/WebResponse";

enum ERROR_CODE {
  UNKNOWN_ERROR = "UNKNOWN_ERROR",
  BAD_CONNECTION = "BAD_CONNECTION",
}

export const ERROR_MESSAGES = {
  [ERROR_CODE.UNKNOWN_ERROR]: "알 수 없는 오류로 실패하였습니다.",
  [ERROR_CODE.BAD_CONNECTION]: "네트워크 상태가 불안정합니다. 잠시 후 다시 시도해주십시오."
};

export interface WebApiRequest {
  url: string;
  method?: "GET" | "POST" | "DELETE" | "PUT";
  contentType?: string;
  data?: any;
}

export async function request({
                                url,
                                method = "GET",
                                contentType = "application/x-www-form-urlencoded",
                                data = {}
                              }: WebApiRequest): Promise<WebResponse> {
  try {
    const isFileUpload = contentType === "multipart/form-data";

    if (!isFileUpload && method === "POST") {
      contentType = "application/json";
      data = JSON.stringify(data);
    }

    const { data: response } = await axios.request({
      url,
      method,
      params: /GET|DELETE/.test(method) ? data : "",
      data: method === "POST" ? data : "",
      headers: {
        "Accept": "application/json",
        "Content-Type": contentType
      }
    });
    if (response.success) {
      return {
        success: true,
        content: response.content || null
      }
    } else {
      return {
        success: false,
        content: response.content || null,
        code: response.code || "",
        message: response.message || ""
      }
    }

  } catch (error) {
    if (error.response) {
      const { status, data } = error.response;
      const message = status === 404 ? ERROR_MESSAGES[ERROR_CODE.BAD_CONNECTION] : data.message;
      if (status === 401) {
        // login();
      }
      if (/404|500/.test(status)) {
        // errorReport(`ajax ${status} error: ${url}`, error);
      }
      return {
        success: false,
        content: null,
        code: status,
        message
      };
    }
    return {
      success: false,
      content: null,
      code: ERROR_CODE.UNKNOWN_ERROR,
      message: ERROR_MESSAGES[ERROR_CODE.UNKNOWN_ERROR]
    };
  }
}
