import React from "react";
import PrevIconUrl from "./svg/ic-arrow-previous.svg";
import NextIconUrl from "./svg/ic-arrow-next.svg";
import "./Arrow.less";

interface Props {
    onClick?: () => void;
    type: "prev" | "next";
    className?: string;
    width?: number | string;
    height?: number | string;
    hide?: boolean;
}

interface States {

}

export default class Arrow extends React.Component<Props, States> {
    handleOnClick = () => {
        this.props.onClick && this.props.onClick();
    };

    render() {
        const { type, width, height, className = "", hide = false } = this.props;

        return (
            <div>
                <button className={`arrow arrow-${type} ${hide && "arrow-hide"} ${className}`} onClick={this.handleOnClick}>
                    { type === "prev" && <img src={PrevIconUrl} alt={"prev"} /> }
                    { type === "next" && <img src={NextIconUrl} alt={"next"} /> }
                </button>
            </div>
        );
    }
}
