import { all, call, put, takeLatest } from "redux-saga/effects";
import { setPostList } from "@Redux/actions/postAction";
import { searchPostApi } from "@Api/postApi";
import { ActionType, GetPostAction } from "@Types/redux/actions";

function* getPostList(action: GetPostAction) {
    const response = yield call(() => searchPostApi({ key: "koko", hashTag: action.hashTag }));

    yield put(setPostList(response.content));
}

export default function* watchRootSaga() {
    return yield all([
        takeLatest(ActionType.GET_POST, getPostList)
    ]);
}
