import Posts from "@Types/model/Posts";

export enum ActionType {
  GET_POST = "GET_POST",
  SET_POST = "SET_POST",
  POST_POSITION_CHANGE = "POST_POSITION_CHANGE",
  CHANGE_DISCOVER_POST_OPEN_STATE = "CHANGE_DISCOVER_POST_OPEN_STATE"
}

export interface GetPostAction {
  type: ActionType.GET_POST;
  hashTag: string;
}

export interface SetPostAction {
  type: ActionType.SET_POST;
  posts: Posts[];
}

export interface PostPositionChangeAction {
  type: ActionType.POST_POSITION_CHANGE,
  position: number;
}

export interface ChangeDiscoverPostOpenStateAction {
  type: ActionType.CHANGE_DISCOVER_POST_OPEN_STATE,
  isPostOpen: boolean;
  position?: number;
}

export type Action =
  | GetPostAction
  | SetPostAction
  | PostPositionChangeAction
  | ChangeDiscoverPostOpenStateAction
