const queryString = require("query-string");
import "./Home.less";
import * as React from "react";
import { connect } from "react-redux";
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { RootState } from "@Types/RootState";
import Posts from "@Types/model/Posts";
import { changePostPosition, getPostList } from "@Redux/actions/postAction";
import Post from "@Components/post/Post";
import Arrow from "@Components/arrow/Arrow";

interface StateProps {
    posts: Posts[];
    currentPosition: number;
}

interface DispatchProps {
}

interface OwnProps {
}

interface Props extends StateProps, DispatchProps, OwnProps, RouteComponentProps {
    children: JSX.Element | JSX.Element[];
    getPostList: Function;
    changePostPosition: Function;
}

interface State {
}

class Home extends React.Component<Props, State> {
    componentDidMount(): void {
        const params = queryString.parse(document.location.search);
        const redirect = params.redirect;
        if (document.location.pathname === '/' && redirect) {
            this.props.history.push(redirect);
        }

        this.props.getPostList();
    }

    render() {
        const { posts, currentPosition } = this.props;
        const { changePostPosition } = this.props;

        return (
            <div className={"home-post"}>
                {
                    posts && posts.length > 0 &&
                        <Post
                            title={posts[currentPosition].title}
                            content={posts[currentPosition].content}
                            author={posts[currentPosition].author}
                            vendorType={posts[currentPosition].vendorType}
                        />
                }
                <div className={"home-arrow"}>
                        <Arrow type={"prev"} hide={currentPosition === 0} onClick={() => changePostPosition(currentPosition - 1)} />
                        <Arrow type={"next"} hide={currentPosition === posts.length} onClick={() => changePostPosition(currentPosition + 1)} />
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state: RootState): StateProps => {
    return {
        posts: state.postState.posts,
        currentPosition: state.postState.currentPosition
    }
};

const mapDispatchToProps = {
    getPostList: getPostList,
    changePostPosition: changePostPosition
};

export default withRouter(connect<StateProps, DispatchProps, OwnProps, RootState>(mapStateToProps, mapDispatchToProps)(Home));
